										</vs-dropdown-menu>
									</vs-dropdown>
								</vs-col>
							</vs-th>
						</template>
						<template slot-scope="{data}">
							<vs-tr :key="indextr" v-for="(tr, indextr) in data">
								<vs-td :data="data[indextr].created">
									<div class="flex items-center">
										<span class="mr-3">{{data[indextr].created}}</span>
										<span class="clock-svg"><feather-icon icon="ClockIcon" :svgClasses="'text-' + data[indextr].status_color" /></span>
