								</vs-td>
								<vs-td :data="data[indextr].dispatcher">
                                    <vs-col vs-type="flex" vs-align="center">
                                        <vs-avatar src="https://avatars2.githubusercontent.com/u/31676496?s=460&v=4"/>
                                        <span class="ml-2">{{data[indextr].dispatcher}}</span>
                                    </vs-col>
								</vs-td>
								<vs-td :data="data[indextr].pickup_at">
